import {ReactNode, useState} from 'react';
import {IndexPath, Layout, MenuItem, OverflowMenu} from '@ui-kitten/components';
import styles from './styles';
import {useTranslation} from 'react-i18next';
import {TouchableOpacity} from 'react-native';
import TextCM from '../Text';
interface IDataMenu {
  id: number;
  key: string;
  value: string;
  icon?: ReactNode;
  selectedTitle?: string;
}
type Props = {
  selectedIndex: IndexPath | undefined;
  setSelectedIndex: (selectedIndex: IndexPath | undefined) => void;
  listData: IDataMenu[];
  selectedTitle?: any;
};

export const OverflowMenuCM = ({
  listData = [],
  selectedIndex = undefined,
  selectedTitle = 'Toggle Menu',
  setSelectedIndex,
}: Props) => {
  const {t} = useTranslation();
  const [visible, setVisible] = useState(false);

  const onItemSelect = (index: IndexPath | undefined): void => {
    setSelectedIndex(index);
    setVisible(false);
  };

  const renderToggleButton = () => (
    // <Button onPress={() => setVisible(true)}>{selectedTitle}</Button>
    <TouchableOpacity onPress={() => setVisible(true)}>
      <TextCM>{selectedTitle}</TextCM>
    </TouchableOpacity>
  );

  return (
    <Layout style={styles.container} level="1">
      <OverflowMenu
        anchor={renderToggleButton}
        visible={visible}
        selectedIndex={selectedIndex}
        onSelect={onItemSelect}
        onBackdropPress={() => setVisible(false)}>
        {listData.map((item: IDataMenu) => (
          <MenuItem title={t(item.value)} />
        ))}
      </OverflowMenu>
    </Layout>
  );
};
